@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:400,600,700|Nunito+Sans:400,700&display=swap);
/*
set the box model to include padding, 
border, and margin in sizing calculations
*/
*,
*:before,
*:after {
  box-sizing: inherit;
  position: relative;
}

/* default removes focus effects */
*:focus {
  outline: none !important;
  box-shadow: none !important;
}

html {
  box-sizing: border-box;
  font: 62.5% var(--font-stack-default); /* this defines what 1 rem is */
  /* 1 rem = 10px; 10px/16px = 62.5% */

  /* @include respond(phone) { // width < 600?
    font: 56.25% var(--font-stack); // 1rem = 9px; 9/16 = 50%
  } */
}


body {
  margin: 0;
  line-height: 1.3;
  font-family: var(--font-stack-default);
  font-size: var(--font-size-default);
  font-weight: var(--font-weight-default);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
}

/* Remove default styling from figures */
figure {
  -webkit-margin-before: 0;
          margin-block-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  -webkit-margin-start: 0;
          margin-inline-start: 0;
  -webkit-margin-end: 0;
          margin-inline-end: 0;
  margin: 0;
}

ul {
  list-style: none;
  padding-left: 0;
}

small {
  font-size: var(--font-size-sm);
}

/* WEbkit autocomplete styling */
input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: var(--form-border);
  /* -webkit-box-shadow: 0 0 0px 1000px #000 inset; */
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

/* standardize strong tags to use specific 
var(--font-weight-bold) font weight for bold */
strong {
  font-weight: var(--font-weight-bold);
}

code {
  font-family: var(--font-stack-code);
}

/* font-family: 'Josefin Sans', sans-serif;
font-family: 'Nunito Sans', sans-serif; */

:root {
  /* color variables */
  --teal-light: #4F8CBB;
  --teal-primary: #2A5270;
  --teal-dark: #1A3346;
  --gold-light: #FFD761;
  --gold-primary: #EEC752;
  --gold-dark: #AD903A;
  --blurple-light: #5D5DC5;
  --blurple-primary: #353579;
  --blurple-dark: #20204C;
  --charcoal-dark: #0B0B0B;
  --charcoal-primary: #373737;
  --charcoal-light: #5D5C5D;
  --gray-light: #fcfcfc;
  --gray-primary: #efefef;
  --gray-dark: #d8d8d8;
  /* font-related variables */
  --font-stack-all: -apple-system, BlinkMacSystemFont, 'Nunito Sans', 'Josefin Sans', sans-serif;
  --font-stack-default: -apple-system, BlinkMacSystemFont, 'Nunito Sans', sans-serif;
  --font-stack-headers: 'Josefin Sans', sans-serif;
  --font-stack-code: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
  --font-size-default: 1.8rem;
  --font-size-sm: 1.5rem;
  --font-weight-default: 400;
  --font-weight-semi-bold: 600;
  --font-weight-bold: 700;
  /* form-related variables */
  --form-border: 1px solid;
  /* border-related variables */
  --radius-default: 5px;
  --border-default: 1px solid black;
  --box-shadow-default: 1.5px 0.5px 1.8px;
}
